import React from 'react';
import AuthTit from '../authTitle';
import {Link} from 'react-router';
require('./loginBox.css');

const LoginBox = React.createClass({
  getInitialState: function () {
    return {
      username: '',
      password: ''
    }
  },
  handelInput: function (e) {
    if (e.target.type === 'text') {
      this.setState({
        username: e.target.value
      })
    } else {
      this.setState({
        password: e.target.value
      })
    }
  },
  handleLogin: function () {
    fetch(apiHost + '/login', {
      method: 'POST',
      body: this.state
    })
      .then(function (res) {
        console.log(res);
      })
  },
  render: function () {
    return (
      <div>
        <AuthTit title=""/>
        <div className="form-inner">
          <form className="form-float-label"  accept-charset="UTF-8">
            <div className="form-group">
              <input className="form-control" autofocus="autofocus" placeholder="" type="email"/>
            </div>

            <div className="form-group">
              <input className="form-control" autocomplete="off" placeholder="" type="password"/>
            </div>
            <div className="form-group action">
              <button className="btn btn-block login-btn"></button>
            </div>
          </form>
          <div className="help-section">
            <span className="btn-alternative">
              <Link to="/auth/register"></Link>
            </span>
            <Link to="/auth/forgetPass"></Link>
            <Link to="/auth/reSendEmail"></Link>
          </div>
        </div>
      </div>
    )
  }
});


export default LoginBox;



